.App {
  text-align: center;
  background-color: #121212;
  width: 100vw;
  height: 100vh;
  /* display: flex; */
  color: white;
}

body {
  padding: 0%;
  margin: 0;
  font-family: "Noto Kufi Arabic", Helvetica, sans-serif;
}

nav {
  height: 60px;
  width: 100%;
  margin: 0;
  border-bottom: 1px solid grey;
  display: grid;
  place-items: center;
}

nav h1 {
  margin: 0;
  font-family: "Noto Kufi Arabic", Arial, sans-serif;
  color: white;
  font-size: 30px;
}
.game {
  width: 100vw;
  height: calc(100vh - 170px);
  display: flex;
  align-items: center;
  padding-top: 50px;
  flex-direction: column;
}

.board {
  width: 300px;
  height: 550px;
  /* border: 1px solid black; */
  display: flex;
  flex-direction: column;
}

.row {
  flex: 33%;
  display: flex;
  flex-direction: row;
  margin: 5px;
}

.letter {
  flex: 33%;
  height: 100%;
  border: 1px solid grey;
  margin: 5px;
  display: grid;
  place-items: center;
  font-size: 17px;
  font-weight: bolder;
  color: white;
  font-family: "Noto Kufi Arabic", Helvetica, sans-serif;
}

#correct {
  background-color: #528d4e;
}

#almost {
  background-color: #b49f39;
}

#error {
  background-color: #3a393c;
}

.keyboard {
  width: 700px;
  height: 300px;
  margin-top: 60px;
}

.line1 {
  flex: 33%;
  display: flex;
  flex-direction: row;
  display: flex;
  justify-content: center;
  margin: 5px;
}
.line2 {
  flex: 33%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 5px;
}
.line3 {
  flex: 33%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 5px;
}

.key {
  width: 50px;
  height: 70px;
  margin: 5px;
  border-radius: 4px;
  display: grid;
  place-items: center;
  font-size: 20px;
  background-color: rgb(75, 75, 75);
  color: white;
  font-family: "Noto Kufi Arabic", Helvetica, sans-serif;
  cursor: pointer;
}

#big1 {
  width: 100px;
  background-color: darkred;
}
#big2 {
  width: 100px;
  background-color: #528d4e;
}
#disabled {
  background-color: #1d1d1d;
}

.playAgainBtn {
  background-color: #4c4c4c; /* Green */
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
  padding: 12px 24px;
  margin-top: 20px;
  cursor: pointer;
  transition: background-color 0.2s ease;
}

.playAgainBtn:hover {
  background-color: #666666;
}

.modalOverlay {
  font-family: "Noto Kufi Arabic", Helvetica, sans-serif;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.modalContent {
  font-family: "Noto Kufi Arabic", Helvetica, sans-serif;
  background-color: #121212;
  color: white;
  padding: 20px;
  border-radius: 8px;
  min-width: 300px;
  text-align: center;
}

.closeAlert {
  font-family: "Noto Kufi Arabic", Helvetica, sans-serif;

}

.helpIcon {
  position: absolute;
  top: 15px;
  right: 20px;
  font-size: 24px;
  color: #fff;
  background-color: #333;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 20;
  font-weight: bold;
  transition: background 0.3s;
}
.helpIcon:hover {
  background-color: #555;
}

.helpModalOverlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
}

.helpModal {
  background-color: #1e1e1e;
  color: #fff;
  padding: 25px;
  border-radius: 15px;
  width: 300px;
  text-align: right;
  direction: rtl;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.4);
}

.helpModal h2 {
  margin-top: 0;
}

.helpModal ul {
  list-style: none;
  padding: 0;
  font-size: 15px;
}

.helpModal li {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.box {
  width: 20px;
  height: 20px;
  display: inline-block;
  border-radius: 4px;
}

.green {
  background-color: #6aaa64;
}

.yellow {
  background-color: #c9b458;
}

.gray {
  background-color: #3a3a3c;
}

/* .red {
  background-color: #5e0000;
} */

.helpModal button {
  margin-top: 15px;
  padding: 8px 20px;
  background-color: #6aaa64;
  border: none;
  color: white;
  border-radius: 8px;
  cursor: pointer;
}

.helpButton {
  position: fixed;
  top: 10px;
  right: 15px;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  background-color: #444;
  color: white;
  font-size: 20px;
  font-weight: bold;
  border: none;
  cursor: pointer;
  z-index: 1000;
  display: flex;
  align-items: center;
  justify-content: center;
  line-height: 0; /* ✅ هذا الجديد */
  transition: background 0.3s;
}


.helpButton:hover {
  background-color: #666;
}

.close-box {
  font-family: "Noto Kufi Arabic", Helvetica, sans-serif;
}

.footer {
  margin-top: 10px;
  text-align: center;
  color: #aaa;
  font-size: 14px;
}

.footer .icon {
  margin-right: 6px;
}

.footer .dev-name {
  color: #6aaa64;
  font-weight: bold;
  text-decoration: none;
  transition: color 0.3s;
}

.footer .dev-name:hover {
  color: #88cc88;
  text-decoration: underline;
}

@media (max-width: 768px) {
  .App {
    height: 100vh;
    overflow: hidden;
    padding-bottom: 0;
  }

  .game {
    padding-top: 20px;
    height: auto;
  }

  .board {
    width: 90vw;
    height: auto;
    margin-bottom: 10px;
  }

  .row {
    margin: 3px;
  }

  .letter {
    font-size: 14px;
    height: 45px;
    margin: 3px;
  }

  .keyboard {
    width: 95vw;
    height: auto;
    margin-top: 20px;
  }

  .key {
    width: 36px;
    height: 48px;
    font-size: 16px;
    margin: 3px;
  }

  #big1,
  #big2 {
    width: 70px;
    font-size: 16px;
  }

  nav h1 {
    font-size: 20px;
  }

  .footer {
    font-size: 12px;
    margin-top: 10px;
    padding-bottom: 10px;
  }

  .helpButton {
    width: 28px;
    height: 28px;
    font-size: 16px;
    top: 12px;
    right: 12px;
  }
}
